<template>
  <v-dialog fullscreen v-model="needLogin" width="500">
    <v-card class="d-flex justify-center">
      <div class="d-flex-none hidden-md-and-up mx-5 mt-13">
        <v-card-title class="d-flex justify-center text-h5" primary-title>
          登录
        </v-card-title>
        <v-card-title
          class="d-flex justify-center subtitle-1"
          style="margin-top: -24px"
          primary-title
        >
          使用您的七天网络账号
        </v-card-title>
        <v-card-actions>
          <v-row class="d-flex justify-center no-gutters">
            <v-col cols="12" sm="12">
              <v-text-field
                label="手机号码"
                prepend-icon="mdi-phone"
                color="#1a73e8"
                outlined
              ></v-text-field>
            </v-col>
            <v-col v-if="!msgLogin" key="pwdf" cols="12" sm="12">
             <form>
              <v-text-field
                label="密码"
                prepend-icon="mdi-lock"
                color="#1a73e8"
                autocomplete="off"
                :append-icon="pwdEye"
                :type="isPwd ? 'password' : 'text'"
                @click:append="
                  pwdEye == 'mdi-eye-off'
                    ? (pwdEye = 'mdi-eye')
                    : (pwdEye = 'mdi-eye-off');
                  isPwd = !isPwd;
                "
                outlined
              ></v-text-field>
             </form>
            </v-col>
            <v-col v-if="msgLogin" key="msgf" md="9">
              <v-otp-input length="6"></v-otp-input>
              <v-btn color="secondary ">text</v-btn>
            </v-col>
            <v-col cols="12" sm="9"></v-col>
            <v-col class="mt-10 align-self-center" sm="6"
              ><v-btn
                style="margin-left: -15px"
                small
                text
                v-html="msgLogin ? '密码登录' : '短信登录'"
                @click="msgLogin = !msgLogin"
                color="#1a73e8"
                >短信登录</v-btn
              ></v-col
            >
            <v-col class="mt-10" sm="6">
              <v-btn
                class="float-right"
                color="#1a73e8"
                dark
                elevation="0"
                @click="$emit('close')"
                >登录</v-btn
              >
            </v-col>
          </v-row>
        </v-card-actions>
      </div>
      <!-- NoCardLay -->

      <v-card
        class="hidden-sm-and-down align-self-center"
        elevation="0"
        style="text-align: center"
        width="448px"
        height="500px"
        rounded="lg"
        outlined
      >
        <v-card-title
          class="justify-center text-h5"
          style="margin-top: 60px"
          primary-title
        >
          登录
        </v-card-title>
        <v-card-actions>
          <v-row class="justify-center no-gutters">
            <v-col sm="10">
              <v-text-field
                width="366px"
                label="手机号码"
                color="#1a73e8"
                outlined
              ></v-text-field>
            </v-col>
            <v-col sm="10">
              <form>
              <v-text-field
                width="366px"
                label="密码"
                autocomplete="off"
                color="#1a73e8"
                :append-icon="pwdEye"
                :type="isPwd ? 'password' : 'text'"
                @click:append="
                  pwdEye == 'mdi-eye-off'
                    ? (pwdEye = 'mdi-eye')
                    : (pwdEye = 'mdi-eye-off');
                  isPwd = !isPwd;
                "
                outlined
              ></v-text-field>
              </form>
            </v-col>
            <v-col class="align-self-center" sm="5"
              ><v-btn
                style="margin-left: -13px"
                class="float-left"
                small
                text
                color="#1a73e8"
                >短信登录</v-btn
              ></v-col
            >
            <v-col sm="5">
              <v-btn
                class="float-right"
                color="#1a73e8"
                dark
                elevation="0"
                @click="$emit('close')"
                >登录</v-btn
              >
            </v-col>
          </v-row>
        </v-card-actions>
      </v-card>
      <!-- CardLay -->
    </v-card>
    <!-- DialogRootCard -->
  </v-dialog>
</template>

<script>
export default {
  name: "LoginDialog",
  props: ["needLogin"],
  data: () => ({
    pwdEye: "mdi-eye-off",
    isPwd: true,
    msgLogin: false,
  }),
};
</script>

